import React from 'react'
import {PlayerTable, playerInformation} from './PlayerPage'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {HeaderTableCell, SquadTableContainer} from '../layout/mu_SquadPage.js';

class SquadPage extends React.Component {

    render() {
        var Players = this.props.players
        return (
            <SquadTableContainer component={Paper}>
                <Table stickyHeader className="squad-table" size="small" >
                    <TableHead className="squad-table-header">
                        <TableRow>
                            <HeaderTableCell></HeaderTableCell>
                            {
                                playerInformation.map((column,index) => {
                                    return(<HeaderTableCell key={index}>{column.Header}</HeaderTableCell>)
                                })
                            }
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            Players.map((player,index) => (
                                <PlayerTable key={index} player={player} index={index}/>
                            ))
                        }
                    </TableBody>
                </Table>
            </SquadTableContainer>
        );
    }   
}
export default SquadPage;