import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import {SearchBox} from '../layout/mu_Search.js';
import {LocalURL} from '../config/Config.js';

class Search extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            squadID: "",
        }
        this.onNameChange = this.onNameChange.bind(this);
    }

    onNameChange = (event) => {
        this.setState({squadID : event.target.value})
    }

    render() {
        var url = LocalURL.concat(this.state.squadID)
        return (
            <SearchBox component={Paper}>
                <Grid 
                    container
                    alignItems="center"
                    justify="center"
                >                    
                    <Grid item sm style={{padding:50}}> 
                        <Typography >Provide the squad ID: </Typography>
                        <TextField 
                            style = {{width: 250}}
                            margin="dense"
                            size="medium"
                            id="squadid"
                            placeholder="Squad ID"
                            variant="filled"
                            type="standard"
                            label=""
                            onChange={this.onNameChange}
                            />
                        <br/>
                        <br/>
                    {
                        this.state.squadID === "" ?                         
                        <Button target="_blank" style={{width: 250}} 
                                variant="contained" color="primary">
                            Load new squad
                        </Button> :                         
                        <Button target="_blank" href={url} style={{width: 250}} 
                                variant="contained" color="primary">
                            Load new squad
                        </Button>
                    }
                    </Grid>
                </Grid>
        </SearchBox>
        );
    }
}
export default Search;