export function ColoredAttribute(attribute) { 

    if(attribute == null)
        return <Typography inline align="right">{''}</Typography>
    
    var value = attribute[1]
    if(value <= 5)
        return <AttributeColoredLowest>{value}</AttributeColoredLowest>
    if(value >= 6 && value <= 10)
        return <AttributeColoredLow>{value}</AttributeColoredLow>
    if(value >= 11 && value <= 15)
        return <AttributeColoredMedium>{value}</AttributeColoredMedium>
    else
        return <AttributeColoredHigh>{value}</AttributeColoredHigh>

}