import React from 'react'
import Table from '@material-ui/core/Table';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import Grid from '@material-ui/core/Grid';
import {PlayerInfoRow, AttributeHeaderTableCell, PlayerAttributesRow, AttributeValueTableCell, ColoredAttribute } from '../layout/mu_PlayerPage';

const technicalAttributes = [
    { accessor: 'Cor', Header: 'Corners' },    
	{ accessor: 'Cro', Header: 'Crossing' },
	{ accessor: 'Dri', Header: 'Dribbling' },
	{ accessor: 'Fin', Header: 'Finishing' },
	{ accessor: 'Fir', Header: 'First Touch' },
	{ accessor: 'Fre', Header: 'Free kick' },
	{ accessor: 'Hea', Header: 'Heading' },
	{ accessor: 'Lon', Header: 'Long shots' },
	{ accessor: 'Lth', Header: 'Long throws' },
	{ accessor: 'Mar', Header: 'Marking' },
	{ accessor: 'Pas', Header: 'Passing' },
	{ accessor: 'Pen', Header: 'Penalty taking' },
	{ accessor: 'Tck', Header: 'Tackling' },
	{ accessor: 'Tec', Header: 'Technique' },
]

const technicalAttributesGoalKeeper = [
    { accessor: 'Aer', Header: 'Aerial Reach' },
	{ accessor: 'Cmd', Header: 'Command of Area' },
	{ accessor: 'Com', Header: 'Communication' },
    { accessor: 'Ecc', Header: 'Eccentricity' },
    { accessor: 'Fir', Header: 'First Touch' },
	{ accessor: 'Han', Header: 'Handling' },
	{ accessor: 'Kic', Header: 'Kicking' },
    { accessor: 'Ovo', Header: '1v1' },
    { accessor: 'Pas', Header: 'Passing' },
    { accessor: 'Ref', Header: 'Reflexes' },
    { accessor: 'Tro', Header: 'Rushing out' },
	{ accessor: 'Pun', Header: 'Punch tendency' },
	{ accessor: 'Thr', Header: 'Throwing' },
]

const mentalAttributes = [
	{ accessor: 'Agg', Header: 'Aggresion' },
	{ accessor: 'Ant', Header: 'Antecipation' },
	{ accessor: 'Bra', Header: 'Bravery' },
	{ accessor: 'Cmp', Header: 'Composure' },
	{ accessor: 'Cnt', Header: 'Concentration' },
	{ accessor: 'Dec', Header: 'Decisions' },
	{ accessor: 'Det', Header: 'Determination' },
	{ accessor: 'Fla', Header: 'Flair' },
	{ accessor: 'Ldr', Header: 'Leadership' },
	{ accessor: 'Otb', Header: 'Off the ball' },
	{ accessor: 'Pos', Header: 'Position' },
	{ accessor: 'Tea', Header: 'Teamwork' },
	{ accessor: 'Vis', Header: 'Vision' },
	{ accessor: 'Wor', Header: 'Work Rate' },
]

const physicalAttributes = [
	{ accessor: 'Acc', Header: 'Acceleration' },
	{ accessor: 'Agi', Header: 'Agility' },
	{ accessor: 'Bal', Header: 'Balance' },
	{ accessor: 'Jum', Header: 'Jumping reach' },
	{ accessor: 'Nat', Header: 'Natural Fittness' },
	{ accessor: 'Pac', Header: 'Pace' },
	{ accessor: 'Sta', Header: 'Stamina' },
	{ accessor: 'Str', Header: 'Strength' },
]

const attributeTypes = [
    {accessor: 'technical', Header: 'Technical'},
    {accessor: 'mental',    Header: 'Mental'},
    {accessor: 'physical',  Header: 'Physical'},
]

export const playerInformation = [           
    { accessor: 'Name',     Header: 'Name' },
    { accessor: 'Position', Header: 'Position'},
    { accessor: 'Age',      Header: 'Age'},
    { accessor: 'Weight',   Header: 'Weight'},
    { accessor: 'Height',   Header: 'Height'},
    { accessor: 'RFoot',    Header: 'Right foot'},
    { accessor: 'LFoot',    Header: 'Left foot'},
];

function buildTechnicalAttributes(technical, isGoalkeeper){
    var technicalRows = []
    if(isGoalkeeper) {
        technicalRows = technicalAttributesGoalKeeper.map(entry => {
            var map = new Map(Object.entries(technical))
        return([entry.Header, map.get(entry.accessor)])
        })
    } else {
        technicalRows = technicalAttributes.map(entry => {
            var map = new Map(Object.entries(technical))
            return([entry.Header, map.get(entry.accessor)])
        })
    }
    return technicalRows
}

function buildMentalAttributes(mental){
    var mentalRows = []
    mentalRows = mentalAttributes.map(entry => {
        const map = new Map(Object.entries(mental))
        return ([entry.Header, map.get(entry.accessor)])
    })
    return mentalRows
}

function buildPhysicalAttributes(physical){
    var physicalRows = []
    physicalRows = physicalAttributes.map(entry => {
        const map = new Map(Object.entries(physical))
        return ([entry.Header, map.get(entry.accessor)])
    })
    return physicalRows
}

function buildAttributesRows(player){

    const attributes = player.Attributes;

    var technical = buildTechnicalAttributes(attributes.Technical, attributes.IsGoalkeeper)
    var mental    = buildMentalAttributes(attributes.Mental)
    var physical  = buildPhysicalAttributes(attributes.Physical)

    var rows = []
    var row = []
    var totalRows = 14
    for(var i = 0; i < totalRows; i++){
        i < technical.length ? row.push(technical[i]) : row.push('')
        i < mental.length    ? row.push(mental[i])    : row.push('')
        i < physical.length  ? row.push(physical[i])  : row.push('')        
        rows.push(row)
        row = []
    }
    return rows
}

export function PlayerTable(props) {

    const { player } = props;
    const [open, setOpen] = React.useState(false);
    var rows = buildAttributesRows(player);
    return (
        <React.Fragment>
        <PlayerInfoRow selected={open} hover={!open}>
            <TableCell>
            <IconButton size="small" onClick={() => setOpen(!open)}>
                {open ? <KeyboardArrowUpIcon/> : <KeyboardArrowDownIcon/>}
            </IconButton>
            </TableCell>
                {playerInformation.map((column, index) => {
                    return(
                        <TableCell key={index}>
                            {player[column.accessor]}
                        </TableCell>
                    )
                })}
            </PlayerInfoRow>
            <PlayerInfoRow selected={open}>
                <TableCell colSpan={8} style={{paddingBottom: 5}}>
                <Collapse in={open} timeout="auto" unmountOnExit>
                <Table size="small">
                    <TableHead>
                        <TableRow>
                            {attributeTypes.map((column, index) => {
                                return(<AttributeHeaderTableCell key={index}>{column.Header}</AttributeHeaderTableCell>)
                            })}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                        rows.map((row, index) => {
                            return(
                            <PlayerAttributesRow key={index}>
                                <AttributeValueTableCell>
                                    <Grid container justify="space-between">  
                                        <Typography align="left">{row[0] != null ? row[0][0] : ''}</Typography>
                                        {ColoredAttribute(row[0])}
                                    </Grid>
                                </AttributeValueTableCell>
                                <AttributeValueTableCell> 
                                    <Grid container justify="space-between">  
                                        <Typography align="left">{row[1] != null ? row[1][0] : ''}</Typography>
                                        {ColoredAttribute(row[1])}
                                    </Grid>
                                </AttributeValueTableCell>
                                <AttributeValueTableCell> 
                                    <Grid container justify="space-between">  
                                        <Typography align="left">{row[2] != null ? row[2][0] : ''}</Typography>
                                        {ColoredAttribute(row[2])}
                                    </Grid>
                                </AttributeValueTableCell>
                            </PlayerAttributesRow>
                            )})
                        }
                    </TableBody>
                </Table>
            </Collapse>
            </TableCell>
        </PlayerInfoRow>
        </React.Fragment>
    );
}
