import React from "react";
import SquadPage from './SquadPage';
import Search from './Search.js';
import axios from 'axios';
import queryString from 'query-string';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import { ThemeProvider } from "@material-ui/styles";
import ListItemText from '@material-ui/core/ListItemText';
import Upload from './Upload'
import Container from '@material-ui/core/Container';
import Toolbar from '@material-ui/core/Toolbar';
import {SquadContainer, MainDrawer, MainAppBar, ListItemDrawer, Theme} from '../layout/mu_View.js';
import {BackendViewEndpoint} from '../config/Config.js';

const theme = Theme;

class View extends React.Component {

    constructor(props) { 
        super(props);
        this.state = {
            squadID : queryString.parse(this.props.location.search).id,
            players : null,
            squadName : "",
            selectedMenuOption : 2,
        }
        this.handleListItemClick = this.handleListItemClick.bind(this);
    }

    async requestSquadInformation() {
        try {
            const response = await axios.get(BackendViewEndpoint, {
                params: {
                    id: this.state.squadID,
                }
            })
            return response.data;
        } catch {
            console.log("error");
        }
    }

    componentDidMount() {
        if (!this.state.squad) {
            this.requestSquadInformation().then(data => {
                this.setState({
                players : data.Players,
                squadName : data.Name,
                selectedMenuOption: 3,
            })}).catch(err => {console.log(err)});
        }
    }

    handleListItemClick(index){
        this.setState({selectedMenuOption : index})
    }

    mainContainer(){
        const currentSquad = this.state.players
        const selectedIndex = this.state.selectedMenuOption

        if(selectedIndex === 1){
            return(<Search></Search>)
        }

        if(selectedIndex === 2){
            return(<Upload></Upload>)
        }

        if(selectedIndex === 3){
            if(!currentSquad){
                return(
                <Container style={{padding: 10}}>
                    <Typography variant="h3" align="center">Squad ID not found<br/></Typography>            
                </Container>)
            } else {
                return <SquadPage players={currentSquad}/>
            }
        }
    }

    render() {

        const handleListItemClick = (e,index) => {
            this.setState({selectedMenuOption : index})
        }

        return (
            <ThemeProvider theme={theme}>
            <CssBaseline />  
                <MainAppBar position="static">
                <Toolbar>
                <Typography variant="h6">{this.state.squadName}</Typography>
                </Toolbar>
                </MainAppBar>
                <MainDrawer anchor="left" variant='permanent'>
                    <div/>
                    <Divider />
                    <List component="nav">
                        <ListItemDrawer>
                            <ListItemText primary="Squadify" />
                        </ListItemDrawer>
                        <ListItemDrawer selected={this.state.selectedMenuOption === 1 ? true : false} 
                            button
                            onClick={(e) => handleListItemClick(e,1)}> 
                            <ListItemText primary="Search for a squad" />
                        </ListItemDrawer>
                        <ListItemDrawer selected={this.state.selectedMenuOption === 2 ? true : false} 
                            button
                            onClick={(e) => handleListItemClick(e,2)}> 
                            <ListItemText primary="Upload new squad" />
                        </ListItemDrawer>
                        <ListItemDrawer selected={this.state.selectedMenuOption === 3 ? true : false} 
                            button 
                            onClick={(e) => handleListItemClick(e,3)}> 
                            <ListItemText primary="View squad" />
                        </ListItemDrawer>
                    </List>
                    </MainDrawer>
                <SquadContainer>
                    {this.mainContainer()}
                </SquadContainer>
            </ThemeProvider>
        )
    } 
}
export default View;
