import React from 'react';
import Upload from './Upload.js';
import View from './View.js';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

class Squadify extends React.Component {
     
    componentDidMount(){
        document.title = "Squadify"
    }

    render() {
      return (
        <Router>
            <div className="Squadify">
                <Switch>
                    <Route path="/" exact component={View}></Route>
                    <Route path="/view/" component={View}></Route>
                    <Route path="/upload" exact component={Upload}></Route>
                </Switch>
            </div>
        </Router>
      );
    }
}

export default Squadify;

